
.blog{margin-top:3rem;padding:0 1rem;font-size:1.4rem;line-height:1.8em
}
.blog .content a{color:#4078c0
}
.blog .content p{margin-bottom:1.2rem
}
.blog .content h1,.blog .content h2,.blog .content h3,.blog .content h4,.blog .content h5,.blog .content h6{margin-bottom:.3rem
}
.blog .content code,.blog .content pre{font-family:'Source Code Pro', Monaco, Consolas, monospace;font-size:1.2rem
}
.blog .content code{padding:.1rem .3rem;background-color:rgba(255,0,0,0.1);color:#c7254e
}
.blog .content pre{padding:1rem;line-height:1.5em;background-color:#eee
}
.blog .content pre code{color:inherit;background-color:transparent
}



